@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom Styles */

/* WinLine Animations */

/* Horizontal and Diagonal Grow */
@keyframes growLine {
   0% {
     transform: scaleX(0);
   }
   50% {
     transform: scaleX(1.1); /* Slightly overshoot */
   }
   100% {
     transform: scaleX(1); /* Final scale */
   }
}

/* Vertical Grow */
@keyframes growVerticalLine {
  from {
    transform: scaleY(0);
  }
  to {
    transform: scaleY(1);
  }
}

/* Animation Classes */
.animate-growLine {
  animation: growLine 0.6s ease-in-out forwards;
}

.animate-growVerticalLine {
  animation: growVerticalLine 0.6s ease-in-out forwards;
}

/* Optional: Remove default button outline on focus and replace with box-shadow */
button:focus {
  outline: none;
}

/* Add transition and hover effects to all buttons */
button {
  transition: background-color 0.3s, transform 0.2s;
}

button:hover {
  transform: scale(1.05);
}

button:active {
  transform: scale(0.95);
}

/* Enhance WinLine appearance */
.absolute.bg-red-500 {
  opacity: 0.8;
  transition: opacity 0.3s;
}

.absolute.bg-red-500:hover {
  opacity: 1;
}

/* Responsive adjustments for smaller screens */
@media (max-width: 640px) {
  .w-16 {
    width: 3rem;
    height: 3rem;
  }
  .text-2xl {
    font-size: 1.5rem;
  }
}

/* Define custom colors if needed */
:root {
  --primary-color: #1D4ED8; /* Example blue */
  --secondary-color: #059669; /* Example green */
  --accent-color: #FF5733; /* Example red */
}

/* Apply colors consistently */
.bg-primary {
  background-color: var(--primary-color);
}

.text-primary {
  color: var(--primary-color);
}

.bg-secondary {
  background-color: var(--secondary-color);
}

.text-secondary {
  color: var(--secondary-color);
}

.bg-accent {
  background-color: var(--accent-color);
}

.text-accent {
  color: var(--accent-color);
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

.shake {
  animation: shake 0.5s;
  animation-iteration-count: 1;
}

/* Add transition to main container */
.flex-col.items-center.justify-center {
  transition: all 0.5s ease-in-out;
}
